                transform: translate3d(-50%, -50%, 0);
                width: auto;
                height: auto;
              "
            >
              <i class="el-icon-upload"></i>
              <div
                class="el-upload__text"
                style="height: auto; line-height: 10vh"
              >
                <em></em>
              </div>
            </div>
            <div
              v-if="showProgress"
              style="
                position: relative;
                top: 50%;
                left: 50%;
