              "
            />
            <img
              v-if="!isShowLocalPhoto && photoUrl"
              id="example"
              class="vjs-default-skin vjs-big-play-centered"
              :src="photoUrl"
              :key="photoName"
              alt="Image"
              style="
                background: #000;
