</template>

<script>
import imagesLoaded from "vue-images-loaded";
export default {
  directives: {
    imagesLoaded,
  },
  data() {
    const item = {
      location: "50,50",
      id: "1234567",
      score: "100",
    };
    return {
      id : "",
      time : 0,
      location: "",
      isDragging: false, // 
      offsetX: 0,
      offsetY: 0,
      currentPage: 1,
      pageNum: 0,
      pageSize: 10,
      tableData: [],
      total: 0,
      haze: false,
      dark: false,
      hdr: false,
      people_detector_enable: false,
      people_tracker_enable: false,
      people_attribute_enable: false,
      vehicle_detector_enable: false,
      vehicle_tracker_enable: false,
      vehicle_press_detector_enable: false,
      vehicle_license_enable: false,
      vehicle_attribute_enable: false,
      vehicle_invasion_enable: false,
      seg_enable: false,
      isShowPhoto: false,
      drawerVisible: false,
      activeIndex: "4",
      showProgress: false,
      progressPercentage: 0,
      photoName: "",
      photoUrl_: "", //,
      isShowLocalPhoto: true,
      uploadUrl: "",
      photoUrl: "",
      detailPhotoName: "",
      logDetail: false,
    };
  },
  created() {
    //  cameraUrl
    this.uploadUrl =  this.$apiBaseUrl + "/upload_photo";
  },
  computed: {
    
    paginatedData() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = this.currentPage * this.pageSize;
      return this.tableData.slice(start, end);
    },
    drawer_class_ctrl() {
      return this.drawerVisible ? "drawer-open" : "drawer-close";
    },
    drawer_button_class_ctrl() {
      return [
        this.drawerVisible ? "drawer-button-open" : "drawer-button-close",
        this.drawerVisible ? "el-icon-caret-right" : "el-icon-caret-left",
      ];
    },
  },
  mounted() {
    this.total = this.tableData.length; // 
    this.$notify({
          title: '',
          message: ':',
          type: 'warning'
        });
  },
  methods: {
    handleButtonClick(row) {
      // 
      console.log(':', row.imageUrl);
      // 
    },
    handleSizeChange(){
      
    },
    handleCurrentChange(){

    },
    startDrag(event) {
      this.isDragging = true; // 
      this.offsetX =
        event.clientX - this.$refs.draggableBox.getBoundingClientRect().left;
      this.offsetY =
        event.clientY - this.$refs.draggableBox.getBoundingClientRect().top;

      document.addEventListener("mousemove", this.doDrag);
      document.addEventListener("mouseup", this.stopDrag);
    },
    doDrag(event) {
      if (this.isDragging) {
        const left = event.clientX - this.offsetX;
        const top = event.clientY - this.offsetY;

        this.$refs.draggableBox.style.left = `${left}px`;
        this.$refs.draggableBox.style.top = `${top}px`;
        this.$refs.draggableBox.style.transform = "none"; //  `transform`
      }
    },
    stopDrag() {
      this.isDragging = false;
      document.removeEventListener("mousemove", this.doDrag);
      document.removeEventListener("mouseup", this.stopDrag);
    },
    closeWindow() {
      this.logDetail = false; //  false 
    },
    async handleButtonClick(id, time, location, photoName) {
      console.log(id, photoName);
      this.id = id;
      this.time = time;
      this.location = location;
      this.detailPhotoName = photoName;
      if (id == "") {
        try {
          const response = await fetch(
             this.$apiBaseUrl + `/stream_photo?name=${this.detailPhotoName}&style=3`
          );

          if (!response.ok) {
            throw new Error("Network response was not ok");
          }

          console.log(response.url);

          this.detailPhotoUrl = response.url; //  URL  src

          console.log(this.detailPhotoUrl);
          this.logDetail = true;
        } catch (error) {
          console.error("There was a problem with the fetch operation:", error);
        }
      } else {
        try {
          const response = await fetch(
             this.$apiBaseUrl + `/stream_video?name=${this.detailPhotoName}&style=4`
          );

          if (!response.ok) {
            throw new Error("Network response was not ok");
          }

          console.log(response.url);

          this.videoUrl = response.url; //  URL  src

          console.log(this.videoUrl);
          this.isShowLocalVideo = false;
          this.isShowVideo = true; // 
        } catch (error) {
          console.error("There was a problem with the fetch operation:", error);
        }
      }
    },
    savePhoto() {
      if (!this.photoName) {
        this.$message({
          type: "error",
          message: "",
        });
      } else {
        fetch(this.photoUrl)
          .then((response) => response.blob()) //  Blob
          .then((blob) => {
            //  URL
            const url = URL.createObjectURL(blob);

            //  <a> 
            const a = document.createElement("a");
            a.style.display = "none";
            a.href = url;
            a.download = "downloaded-image.jpg"; // 

            //  <a>  DOM 
            document.body.appendChild(a);
            a.click();

            //  URL  <a> 
            URL.revokeObjectURL(url);
            document.body.removeChild(a);
          })
          .catch((error) => {
            this.$message({
              type: "error",
              message: "",
            });
          });
      }
    },
    imageProgress(instance, image) {
      const result = image.isLoaded ? "loaded" : "broken";
    },
    sendParameters() {
      let data = {
        haze: this.haze,
        dark: this.dark,
        hdr: this.hdr,
        people_detector: this.people_detector_enable,
        people_tracker: this.people_tracker_enable,
        people_attr_detector: this.people_attribute_enable,
        vehicle_tracker: this.vehicle_tracker_enable,
        vehicle_detector: this.vehicle_detector_enable,
        vehicle_attr_detector: this.vehicle_attribute_enable,
        vehicleplate_detector: this.vehicle_license_enable,
        vehicle_press_detector: this.vehicle_press_detector_enable,
        vehicle_invasion: this.vehicle_invasion_enable,
        seg_enable:this.seg_enable
      };
      return this.$axios
        .post( this.$apiBaseUrl+"/ConfirmParams", data)
        .then((res) => {
          
        });
    },
    checkParameter(value) {
      var that = this;
      if (value) {
        // 
        const people_list = [that.people_attribute_enable];
        for (let i = 0; i < people_list.length; i++) {
          if (people_list[i]) {
            that.people_tracker_enable = true;
          }
        }
        const vehicle_list = [
          that.vehicle_attribute_enable,
          that.vehicle_license_enable,
          that.vehicle_press_detector_enable,
          that.vehicle_invasion_enable,
        ];
        for (let i = 0; i < vehicle_list.length; i++) {
          if (vehicle_list[i]) {
            that.vehicle_tracker_enable = true;
          }
        }
      } else {
        // 
        if (!(that.people_detector_enable && that.people_tracker_enable)) {
          that.people_attribute_enable = false;
        }
        if (!(that.vehicle_detector_enable && that.vehicle_tracker_enable)) {
          that.vehicle_attribute_enable = false;
          that.vehicle_license_enable = false;
          that.vehicle_press_detector_enable = false;
          that.vehicle_invasion_enable = false;
        }
      }
    },
    async dealwithPhoto() {
  let loadingInstance; //  try  loadingInstance catch 
      console.log("123");
      
  try {
    //  loading
    loadingInstance = this.$loading({
      lock: true,
      text: `...`, // 
      spinner: "el-icon-loading",
      background: "rgba(0, 0, 0, 0.7)",
    });

    // 
    await this.sendParameters();

    let data = {
      name: this.photoName,
    };
    console.log(data);

    // 
    await this.$axios.post( this.$apiBaseUrl+"/start_process_photo", data);


    // 
    await this.getPhoto();
    // await this.getLog();
  } catch (error) {
    console.error(":", error);
  } finally {
    //  loading 
    if (loadingInstance) {
      loadingInstance.close();
    }
  }
},


    resetPhoto() {
      this.isShowPhoto = false;
      this.isShowLocalPhoto = true;
      this.photoName = "";
      this.photoUrl_ = "";
    },
    async getPhoto() {
  let flag = 0; // 
  const checkInterval = 1000; // 
  
  while (flag === 0) {
    try {
      const response = await fetch( this.$apiBaseUrl+`/stream_photo?name=${this.photoName}&style=2`);
      
      if (!response.ok) {
        throw new Error("Network response was not ok");
      }

      const url = response.url; //  URL
      
      //  url 
      const contentResponse = await fetch(url); //  url 
      
      if (!contentResponse.ok) {
        throw new Error("Failed to fetch content from the URL");
      }

      const contentType = contentResponse.headers.get("Content-Type");

      if (contentType && contentType.includes("application/json")) {
        //  JSON
        const jsonResponse = await contentResponse.json(); //  JSON 
        console.log(jsonResponse); //  JSON 
        //  JSON  success  0
        if (jsonResponse.success === 0) {
          console.log("...");
          flag = 0; // 
        }
      } else if (contentType && contentType.includes("image/jpeg")) {
        // 
        this.photoUrl = url; //  URL  src
        console.log("URL:", this.photoUrl);
        flag = 1; //  1
        this.isShowPhoto = true; // 
        this.isShowLocalPhoto = false; // 
      } else {
        console.log("");
      }
    } catch (error) {
      console.error(":", error);
    }

    // 
    await new Promise(resolve => setTimeout(resolve, checkInterval));
  }
},

    toggleDrawer() {
      this.drawerVisible = !this.drawerVisible;
    },
    handleBeforeUpload(file) {
      const supportedFormats = ["jpg", "jpeg", "png", "gif"];
      const fileExtension = file.name.split(".").pop().toLowerCase();
      const isSupportedFormat = supportedFormats.includes(fileExtension);

      const maxSize = 100 * 1024 * 1024; // 100 MB
      const isSizeValid = file.size <= maxSize;

      if (isSupportedFormat && isSizeValid) {
        this.showConfirmDialog(file);
        return false; // 
      } else {
        let errorMessage = "";
        if (!isSupportedFormat) {
          errorMessage += "";
        }
        if (!isSizeValid) {
          errorMessage += " 100 MB";
        }
        this.$message.error(errorMessage);
        return false;
      }
    },
    showConfirmDialog(file) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.uploadFile(file);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    uploadFile(file) {
  const reader = new FileReader();
  reader.onload = (e) => {
    this.isShowPhoto = true;
    this.isShowLocalPhoto = true;
    this.photoUrl_ = e.target.result; //  data URL  photoUrl
  };
  reader.readAsDataURL(file);

  const formData = new FormData();
  formData.append("photo", file);

  this.$axios
    .post(this.uploadUrl, formData, {
      headers: {
        'Authorization': 'token5370ffbdcb08f3a7bfbf75287582539842d93c30', //  Authorization 
        'Content-Type': 'multipart/form-data' //  multipart/form-data
      }
    })
    .then((response) => {
      this.handleSuccess(response, file);
    })
    .catch((error) => {
      this.handleError(error, file);
    });
},
    handleSuccess(response, file) {
      this.$message({
        type: "success",
        message: "!",
      });
      this.photoName = response.data.photoname;
    },
    handleError(error, file) {
      this.$message({
        type: "error",
      });
      console.error(":", error);
    },
    handleProgress(event, file) {
      console.log(":", event.percent);
    },
    onImageLoad() {
      console.log("");
    },
    onImageError() {
      console.error("");
    },
  },
};
</script>

<style scoped>
.right-log {
  width: 80%;
  height: 100%;
  background-color: white;
  margin-left: 10%;
  margin-right: 10%;
  border-radius: 8px;
}
.right-log-head {
  height: 5vh;
  width: 100%;
  background-color: rgb(66, 159, 255);
  color: white;
  font-size: 1.5vw;
  line-height: 5vh;
  border-top-right-radius: 8px; /* :  */
  border-top-left-radius: 8px;
}
.bottom-ctrl {
  border-radius: 8px; /* :  */
  background-color: white;
  margin-top: 2vh;
  margin-bottom: 4vh;
  display: flex; /*  Flexbox */
  flex-direction: column; /*  */
  justify-content: space-evenly; /*  */
}
.bottom-ctrl-one {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.bottom-button {
  height: 8vh;
  width: 15vw;
  font-size: 1.5vw;
  margin-left: 0.5vw;
  margin-right: 0.5vw;
}
.upload {
  display: flex;
  flex-direction: column; /*  */
  justify-content: center; /*  */
  align-items: center; /*  */
  height: 100%; /*  */
  width: 100%; /*  */
  border: 1px dashed #dcdfe6; /* :  */
  border-radius: 8px; /* :  */
  margin-bottom: -2vh;
}
.upload div {
  width: 100%;
  height: 100%;
}
.upload .el-icon-upload {
  font-size: 20vw; /*  */
  margin-bottom: 3vh; /*  */
}

.upload .el-upload__text {
  text-align: center; /*  */
  font-size: 3vw;
}
/deep/ .el-upload {
  width: 100%;
  height: 100%;
}
/deep/ .el-upload .el-upload-dragger {
  width: 100%;
  height: 100%;
}

.main {
  display: flex;
  height: 90%;
  width: 100%;
  justify-content: space-evenly;
}
.drawer-open {
  position: fixed;
  top: 7vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(0);
  transition: transform 0.3s ease;
  user-select: none;
}

.drawer-close {
  position: fixed;
  top: 7vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(14vw);
  transition: transform 0.3s ease;
  user-select: none;
}

.drawer-content {
  height: 100%;
  overflow-y: auto;
}
.drawer-content::-webkit-scrollbar {
  display: none; /*  Chrome, Safari, Edge  */
}

.drawer-button-bar {
  background-color: white;
  height: 100%;
  width: 1vw;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.drawer-button-open {
  position: absolute;
  top: 50%;
  left: 100%; /*  */
  transform: translate3d(-100%, -50%, 0);
  transition: all 0.5s;
}

.drawer-button-close {
  position: absolute;
  top: 50%;
  left: 100%; /*  */
  transform: translate3d(-100%, -50%, 0) rotateX(180deg);
  transition: all 0.5s;
}
video::-webkit-media-controls-play-button {
  display: none;
}
video::-webkit-media-controls-timeline {
  display: none;
}
</style>



