<template>
  <div style="width: 100%; height: 100%">
    <!--  -->
    <div :class="drawer_class_ctrl" style="width: 15vw">
      <div class="drawer-content">
        <div
          class="right-log-head"
          style="line-height: 6vh; position: absolute; z-index: 5; height: 6vh"
        >
          
        </div>
        <div style="height: 3vh"></div>
        <el-divider></el-divider>
        <div style="user-select: none">
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="haze"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
        <el-divider></el-divider>
        <div style="user-select: none">
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="dark"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
        <el-divider></el-divider>
        <div style="user-select: none">
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="people_tracker_enable"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
        <el-divider></el-divider>
        <div style="user-select: none">
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="people_attribute_enable"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
        <el-divider></el-divider>
        <div style="user-select: none">
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="vehicle_tracker_enable"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
        <el-divider></el-divider>
        <div style="user-select: none">
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="vehicle_attribute_enable"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
        <el-divider></el-divider>
        <div style="user-select: none">
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="seg_enable"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
        <el-divider></el-divider>
        <div style="user-select: none">
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="vehicle_license_enable"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
        <el-divider></el-divider>
        <div style="user-select: none">
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="vehicle_press_detector_enable"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
        <el-divider></el-divider>
        <div>
          <div style="height: 4vh; line-height: 4vh; user-select: none">
            
          </div>
          <el-switch
            v-model="vehicle_invasion_enable"
            active-text=""
            inactive-text=""
            @change="checkParameter"
          >
          </el-switch>
        </div>
      </div>
      <div class="drawer-button-bar" @click="toggleDrawer">
        <!--  toggleDrawer  -->
        <i :class="drawer_button_class_ctrl"></i>
      </div>
    </div>

    <!--  -->
    <div style="height: 5%"></div>
    <div class="main">
      <div style="height: 100%; width: 70%">
        <div style="height: 80%; width: 100%" class="upload">
          <!--   -->
          <el-upload
            class="upload-demo"
            drag
            :action="uploadUrl"
            multiple
            :before-upload="handleBeforeUpload"
            :on-success="handleSuccess"
            :on-error="handleError"
            :on-progress="handleProgress"
            v-if="!isShowVideo"
          >
            <div
              v-if="!showProgress"
              style="
                position: relative;
                top: 50%;
                left: 50%;
                transform: translate3d(-50%, -50%, 0);
                width: auto;
                height: auto;
              "
            >
              <i class="el-icon-upload"></i>
              <div
                class="el-upload__text"
                style="height: auto; line-height: 10vh"
              >
                <em></em>
              </div>
            </div>
          </el-upload>
          <!--   -->
          <!--   -->
          <!-- <div v-if="isLoading">
      <el-loading :text="`: ${progressPercentage}%`" lock fullscreen z-index="9999">
      </el-loading>
    </div> -->
          <div
            v-if="isShowVideo"
            style="background-color: #000; width: 100%; height: 100%"
          >
            <video
              v-if="isShowLocalVideo"
              controls
              preload="auto"
              style="
                background: #000;
                max-width: 100%;
                max-height: 100%;
                object-fit: contain;
              "
              :src="VideoUrl_"
              type="video/mp4"
            ></video>
            <video
              v-if="!isShowLocalVideo"
              id="example"
              controls
              preload="auto"
              style="
                background: #000;
                max-width: 100%;
                max-height: 100%;
                object-fit: contain;
              "
              :src="videoUrl"
              type="video/mp4"
            ></video>
          </div>
          <!--   -->
          <!---->
          <div v-if="logDetail"
     ref="draggableBox"
     style="height: 80%; width: 60%; position: absolute; left: 50%; top: 50%; transform: translate(-50%,-50%); z-index: 8; background-color: white; border-radius: 0.5vw;">
  <!--  -->
  <div @mousedown="startDrag"
       style="cursor: move; width: 100%; height: 7vh; border-top-left-radius: 0.5vw; border-top-right-radius: 0.5vw; background-color: rgb(66,159,255); text-align: center; line-height: 7vh; font-size: 2vw; color: white; font-weight: 900;">
    

    <!--  -->
    <button @click="closeWindow" 
            style="position: absolute; right: 10px; top: 10px; background: transparent; border: none; font-size: 2vw; color: white; cursor: pointer;">
      
    </button>
  </div>

  <!--  flex  -->
  <div style="display: flex; height: calc(80% - 7vh); padding: 10px;">

    <!--  -->
    <div style="flex: 3; display: flex; justify-content: center; align-items: center;padding-top: 40px;" >
      <img :src="detailPhotoUrl" alt="image" style="width: 75%; height: auto; border-radius: 0.5vw;">
    </div>

    <!--  -->
    <div style="flex: 1.3; padding: 20px; display: flex; flex-direction: column; justify-content: center; margin-left: -30px;">
  <div style="font-size: 1.8vw; font-weight: 700; margin-bottom: 10px;"></div>
  
  <!--  -->
  <div style="font-size: 1.8vw; color: gray; margin-bottom: 10px;">
    : <br>
    <span style="font-weight: normal;">{{ id }}</span>
  </div>

  <!--  -->
  <div style="font-size: 1.8vw; color: gray; margin-bottom: 10px;">
    : <br>
    <span style="font-weight: normal;">{{ time }}</span>
  </div>

  <!--  -->
  <div style="font-size: 1.8vw; color: gray;">
    : <br>
    <span style="font-weight: normal;">{{ location }}</span>
  </div>
</div>

  </div>
</div>

          <!---->
        </div>
        <div style="height: 5%"></div>
        <!-- -->
        <div
          class="bottom-ctrl"
          style="height: 15%; width: 100%; font-size: 2vw"
        >
          <div class="bottom-ctrl-one">
            <!-- <el-button type="primary" class="bottom-button" @click="switchCamera">{{isShowCamera ? '' : ''}}</el-button>
                <el-button type="primary" class="bottom-button"></el-button> -->
            <!-- <el-button type="primary" class="bottom-button"></el-button> -->
            <el-button
              type="primary"
              class="bottom-button"
              @click="dealWithVideo"
              ></el-button
            >
            <el-button type="primary" class="bottom-button" @click="saveVideo"
              ></el-button
            >
            <el-button type="primary" class="bottom-button"></el-button>
            <el-button type="primary" class="bottom-button" @click="resetVideo"
              ></el-button
            >
          </div>
        </div>
        <!-- -->
      </div>

      <div style="height: 100%; width: 30%">
        <div class="right-log">
          <div class="right-log-head"></div>
          <el-table
            ref="multipleTable"
            :data="paginatedData"
            tooltip-effect="dark"
            style="width: 100%"
            :row-style="{ height: '50px' }"
          >
            <el-table-column width="5"> </el-table-column>
            <el-table-column prop="id" label="" width="60">
              <!-- <template slot-scope="scope">{{ scope.row.date }}</template> -->
            </el-table-column>
            <el-table-column prop="time" label="" >
            </el-table-column>
            <el-table-column prop="location" label="" width="150">
            </el-table-column>
            <el-table-column label="" >
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  class="chaKanButton"
                  @click="handleButtonClick(scope.row.id,scope.row.time,scope.row.location, scope.row.name)"
                >
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="padding: 20px; text-align: center ;font-size: 3.5vw;">
            <el-pagination
              :current-page="pageNum"
              :page-sizes="[10, 20, 50]"
              :page-size="pageSize"
              layout="total, prev, pager, next, jumper"
              :total="total"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!--  -->
</template>

<script>
import myvideo from "../../video/myvideo.vue";
export default {
  components: {
    myvideo,
  },
  data() {
    return {
      id : "",
      time : 0,
      location: "",
      isDragging: false, // 
      offsetX: 0,
      offsetY: 0,
      currentPage: 1,
      pageNum: 0,
      pageSize: 10,
      tableData: [],
      total: 0,
      haze: false,
      dark: false,
      people_detector_enable: false, // 
      people_tracker_enable: false,
      people_attribute_enable: false,
      vehicle_detector_enable: false, //
      vehicle_tracker_enable: false,
      vehicle_press_detector_enable: false,
      vehicle_license_enable: false,
      vehicle_attribute_enable: false,
      vehicle_invasion_enable: false,
      seg_enable:false,
      isShowVideo: false,
      drawerVisible: false,
      activeIndex: "4", // 
      videoUrl: "",
      uploadUrl: "",
      showProgress: false,
      progressPercentage: 0,
      isShowLocalVideo: true,
      VideoUrl_: "",
      detailPhotoName: "",
      logDetail: false,
    };
  },
  computed: {
    paginatedData() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = this.currentPage * this.pageSize;
      return this.tableData.slice(start, end);
    },

    drawer_class_ctrl() {
      return this.drawerVisible ? "drawer-open" : "drawer-close";
    },
    drawer_button_class_ctrl() {
      return [
        this.drawerVisible ? "drawer-button-open" : "drawer-button-close",
        this.drawerVisible ? "el-icon-caret-right" : "el-icon-caret-left",
      ];
    },
  },
  created() {
    //  cameraUrl
    this.uploadUrl =  this.$apiBaseUrl + "/uploadVideo";
  },
  mounted() {
    this.total = this.tableData.length; // 
    this.$notify({
          title: '',
          message: ':',
          type: 'warning'
        });
  },
  methods: {
    startDrag(event) {
      this.isDragging = true; // 
      this.offsetX =
        event.clientX - this.$refs.draggableBox.getBoundingClientRect().left;
      this.offsetY =
        event.clientY - this.$refs.draggableBox.getBoundingClientRect().top;

      document.addEventListener("mousemove", this.doDrag);
      document.addEventListener("mouseup", this.stopDrag);
    },
    doDrag(event) {
      if (this.isDragging) {
        const left = event.clientX - this.offsetX;
        const top = event.clientY - this.offsetY;

        this.$refs.draggableBox.style.left = `${left}px`;
        this.$refs.draggableBox.style.top = `${top}px`;
        this.$refs.draggableBox.style.transform = "none"; //  `transform`
      }
    },
    stopDrag() {
      this.isDragging = false;
      document.removeEventListener("mousemove", this.doDrag);
      document.removeEventListener("mouseup", this.stopDrag);
    },
    closeWindow() {
      this.logDetail = false; //  false 
    },
    async handleButtonClick(id, time, location, photoName) {
      console.log(id, photoName);
      this.id = id;
      this.time = time;
      this.location = location;
      this.detailPhotoName = photoName;
      if (id == "") {
        try {
          const response = await fetch(
             this.$apiBaseUrl +  `/stream_photo?name=${this.detailPhotoName}&style=3`
          );

          if (!response.ok) {
            throw new Error("Network response was not ok");
          }

          console.log(response.url);

          this.detailPhotoUrl = response.url; //  URL  src

          console.log(this.detailPhotoUrl);
          this.logDetail = true;
        } catch (error) {
          console.error("There was a problem with the fetch operation:", error);
        }
      } else {
        try {
          const response = await fetch(
             this.$apiBaseUrl + `/stream_video?name=${this.detailPhotoName}&style=4`
          );

          if (!response.ok) {
            throw new Error("Network response was not ok");
          }

          console.log(response.url);

          this.videoUrl = response.url; //  URL  src

          console.log(this.videoUrl);
          this.isShowLocalVideo = false;
          this.isShowVideo = true; // 
        } catch (error) {
          console.error("There was a problem with the fetch operation:", error);
        }
      }
    },
    handleSizeChange() {},
    handleCurrentChange(page) {
      this.currentPage = page;
    },
    saveVideo() {
      if (!this.videoName) {
        this.$message({
          type: "error",
          message: "",
        });
      } else {
        const videoUrl = `${this.$apiBaseUrl}/stream_video?name=${this.videoName}&style=2`;
        fetch(videoUrl)
          .then((response) => response.blob()) //  Blob
          .then((blob) => {
            //  URL
            const url = URL.createObjectURL(blob);

            //  <a> 
            const a = document.createElement("a");
            a.style.display = "none";
            a.href = url;
            a.download = "downloaded-video.mp4"; // 

            //  <a>  DOM 
            document.body.appendChild(a);
            a.click();

            //  URL  <a> 
            URL.revokeObjectURL(url);
            document.body.removeChild(a);
          })
          .catch((error) => {
            this.$message({
              type: "error",
              message: "",
            });
          });
      }
    },
    sendParameters() {
      let data = {
        haze: this.haze,
        dark: this.dark,
        hdr: false,
        people_detector: this.people_detector_enable,
        people_tracker: this.people_tracker_enable,
        people_attr_detector: this.people_attribute_enable,
        vehicle_tracker: this.vehicle_tracker_enable,
        vehicle_detector: this.vehicle_detector_enable,
        vehicle_attr_detector: this.vehicle_attribute_enable,
        vehicleplate_detector: this.vehicle_license_enable,
        vehicle_press_detector: this.vehicle_press_detector_enable,
        vehicle_invasion: this.vehicle_invasion_enable,
        seg_enable:this.seg_enable
      };
      return this.$axios
        .post(  this.$apiBaseUrl + "/ConfirmParams", data)
        .then((res) => {});
    },
    checkParameter(value) {
      var that = this;
      if (value) {
        // 
        const people_list = [that.people_attribute_enable];
        for (let i = 0; i < people_list.length; i++) {
          if (people_list[i]) {
            that.people_tracker_enable = true;
          }
        }
        const vehicle_list = [
          that.vehicle_attribute_enable,
          that.vehicle_license_enable,
          that.vehicle_press_detector_enable,
          that.vehicle_invasion_enable,
        ];
        for (let i = 0; i < vehicle_list.length; i++) {
          if (vehicle_list[i]) {
            that.vehicle_tracker_enable = true;
          }
        }
      } else {
        // 
        if (!(that.people_detector_enable && that.people_tracker_enable)) {
          that.people_attribute_enable = false;
        }
        if (!(that.vehicle_detector_enable && that.vehicle_tracker_enable)) {
          that.vehicle_attribute_enable = false;
          that.vehicle_license_enable = false;
          that.vehicle_press_detector_enable = false;
          that.vehicle_invasion_enable = false;
        }
      }
    },
    resetVideo() {
      this.isShowVideo = false;
      this.isShowLocalVideo = true;
      this.videoName = "";
      this.videoUrl_ = "";
    },
    // dealWithVideo() {
    //   this.sendParameters()
    //     .then(() => {
    //       let data = {
    //         name: this.videoName,
    //       };
    //       console.log(data);
    //       return this.$axios.post(
    //         "http://localhost:8000/start_process_video",
    //         data
    //       );
    //     })
    //     .then(() => {
    //       this.getVideo();
    //     })
    //     .catch((error) => {
    //       console.error(":", error);
    //     });
    // },
    async dealWithVideo() {
      try {
        // 
        await this.sendParameters();

        let data = {
          name: this.videoName,
        };
        console.log(data);

        // 
        await this.$axios.post(
           this.$apiBaseUrl + "/start_process_video",
          data
        );
        console.log(this.progressPercentage);
        
        //  loading
        const loadingInstance = this.$loading({
          lock: true,
          text: `...${this.progressPercentage}%`,
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)",
        });

        // 
        await this.checkProgress(loadingInstance);

        // 100%
        await this.getVideo();
        await this.getLog();
        //  loading
        loadingInstance.close();
      } catch (error) {
        console.error(":", error);
        loadingInstance.close(); //  loading
      }
    },

    async getLog() {
      try {
        const response = await this.$axios.post( this.$apiBaseUrl+"/log");
        console.log(response);
        if (response.status === 200) {
          const convertedPeopleLog = response.data.people_log.map((item) => {
            return {
              id: "",
              time: item[0],
              location: `(${Math.floor(item[1][0])}, ${Math.floor(
                item[1][1]
              )}, ${Math.floor(item[1][2])}, ${Math.floor(item[1][3])})`,
              name: item[2],
            };
          });

          //  vehicle_log
          const convertedVehicleLog = response.data.vehicle_log.map((item) => {
            return {
              id: "",
              time: item[0],
              location: `(${Math.floor(item[1][0])}, ${Math.floor(
                item[1][1]
              )}, ${Math.floor(item[1][2])}, ${Math.floor(item[1][3])})`,
              name: item[2],
            };
          });

          // 
          const combinedLogs = [...convertedPeopleLog, ...convertedVehicleLog];

          //  paginatedData
          this.tableData = combinedLogs;
          this.total = this.tableData.length;
          console.log(this.tableData);
        }
      } catch (error) {
        console.error(
          ":",
          error.response ? error.response.data : error
        );
      }
    },
    // 
    async checkProgress(loadingInstance) {
      let progress = 0;
      while (progress < 100) {
        try {
          let data = {
            video_name: this.videoName,
          };
          const response = await this.$axios.post(
             this.$apiBaseUrl + "/get_progress",
            data
          );

          if (response.status === 200 && response.data.progress !== undefined) {
            progress = Math.round(response.data.progress); //  `progress` 
            console.log(`: ${progress}%`);
            this.progressPercentage = progress; // 
            loadingInstance.setText(`... ${this.progressPercentage}%`);
          } else {
            console.warn("Invalid response format");
          }
        } catch (error) {
          console.error("There was a problem with the fetch operation:", error);
        }
        // 
        await new Promise((resolve) => setTimeout(resolve, 1000));
      }
      this.progressPercentage = 0;
    },

    //  sleep 
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    async getVideo() {
      try {
        const response = await fetch(
           this.$apiBaseUrl + `/stream_video?name=${this.videoName}&style=2`
        );

        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        console.log(response.url);

        this.videoUrl = response.url; //  URL  src
        console.log(this.videoUrl);
        this.isShowLocalVideo = false;
        this.isShowVideo = true; // 
      } catch (error) {
        console.error("There was a problem with the fetch operation:", error);
      }
    },
    toggleDrawer() {
      this.drawerVisible = !this.drawerVisible;
    },
    handleBeforeUpload(file) {
      const supportedFormats = ["mp4"];
      const fileExtension = file.name.split(".").pop().toLowerCase();
      const isSupportedFormat = supportedFormats.includes(fileExtension);

      const maxSize = 100 * 1024 * 1024; // 100 MB
      const isSizeValid = file.size <= maxSize;

      if (isSupportedFormat && isSizeValid) {
        this.showConfirmDialog(file);
        return false; // 
      } else {
        let errorMessage = "";
        if (!isSupportedFormat) {
          errorMessage += "mp4";
        }
        if (!isSizeValid) {
          errorMessage += " 100 MB";
        }
        this.$message.error(errorMessage);
        return false;
      }
    },
    showConfirmDialog(file) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          // 
          this.uploadFile(file);
        })
        .catch(() => {
          // 
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    handleError(error, file) {
      console.error(":", error);
      this.$message.error("");
    },
    handleSuccess(response, file) {
      this.$message({
        type: "success",
        message: "!",
      });
      const reader = new FileReader();
      reader.onload = (e) => {
        this.VideoUrl_ = e.target.result;
        this.isShowVideo = true;
      };
      reader.readAsDataURL(file);
      // 
    },
    handleProgress(event, file, fileList) {
      // this.progressPercentage = event.percent;
      // this.showProgress = true;
      // console.log(':', event.percent);
    },
    uploadFile(file) {
      const formData = new FormData();
      formData.append("video", file);

      const config = {
        headers: {
          "Content-Type": "multipart/form-data",
        },
        /*onUploadProgress: (progressEvent) => {
          const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          this.progressPercentage = percentCompleted;
          this.showProgress = true;
        }*/
      };
      // this.startProgressPolling();
      this.$axios
        .post(this.uploadUrl, formData, config)
        .then((response) => {
          this.handleSuccess(response, file);
          this.videoName = response.data.videoname;
        })
        .catch((error) => {
          this.handleError(error, file);
        });
      /*this.$axios.post(this.uploadUrl, formData, config).then(response => {
        this.videoName = response.data.videoname; //  { video_name: 'some-video-name' }
        console.log('Video name:', this.videoName);
        //this.startProgressPolling(); // 
        this.handleSuccess(response, file);
      }).catch(error => {
        this.handleError(error, file);
      });*/
    },
    startProgressPolling() {
      data = {
        name: this.videoName,
      };
      this.$axios
        .post( this.$apiBaseUrl+"/get_progress", data)
        .then((res) => {
          this.progress = res.progress;
        });
      const loading = this.$loading({
        lock: true,
        text: thisprogress,
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
      setTimeout(() => {
        loading.close();
      }, 2000);
    },
  },
};
</script>

<style scoped>
.right-log {
  width: 80%;
  height: 100%;
  background-color: white;
  margin-left: 10%;
  margin-right: 10%;
  border-radius: 8px;
}

.chaKanButton {
  width: 100%;
  font-size: small;
}

.right-log-head {
  height: 5vh;
  width: 100%;
  background-color: rgb(66, 159, 255);
  color: white;
  font-size: 1.5vw;
  line-height: 5vh;
  border-top-right-radius: 8px;
  /* :  */
  border-top-left-radius: 8px;
}

.bottom-ctrl {
  border-radius: 8px;
  /* :  */
  background-color: white;
  margin-top: 2vh;
  margin-bottom: 4vh;
  display: flex;
  /*  Flexbox */
  flex-direction: column;
  /*  */
  justify-content: space-evenly;
  /*  */
}

.bottom-ctrl-one {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.bottom-button {
  height: 8vh;
  width: 15vw;
  font-size: 1.5vw;
  margin-left: 0.5vw;
  margin-right: 0.5vw;
}

.upload {
  display: flex;
  flex-direction: column;
  /*  */
  justify-content: center;
  /*  */
  align-items: center;
  /*  */
  height: 100%;
  /*  */
  width: 100%;
  /*  */
  border: 1px dashed #dcdfe6;
  /* :  */
  border-radius: 8px;
  /* :  */
  margin-bottom: -2vh;
}

.upload div {
  width: 100%;
  height: 100%;
}

.upload .el-icon-upload {
  font-size: 20vw;
  /*  */
  margin-bottom: 3vh;
  /*  */
}

.hidden-column .cell {
  display: none;
}

.upload .el-upload__text {
  text-align: center;
  /*  */
  font-size: 3vw;
}

/deep/ .el-upload {
  width: 100%;
  height: 100%;
}

/deep/ .el-upload .el-upload-dragger {
  width: 100%;
  height: 100%;
}

.main {
  display: flex;
  height: 90%;
  width: 100%;
  justify-content: space-evenly;
}

.drawer-open {
  position: fixed;
  top: 10vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(0);
  transition: transform 0.3s ease;
  border-top-right-radius: 8px;
  /* :  */
  border-top-left-radius: 8px;
  user-select: none;
}

.drawer-close {
  position: fixed;
  top: 10vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(14vw);
  transition: transform 0.3s ease;
  border-top-right-radius: 8px;
  /* :  */
  border-top-left-radius: 8px;
  user-select: none;
}

.drawer-content {
  height: 100%;
  overflow-y: auto;
}

.drawer-content::-webkit-scrollbar {
  display: none;
  /*  Chrome, Safari, Edge  */
}

.drawer-button-bar {
  background-color: white;
  height: 100%;
  width: 1vw;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.drawer-button-open {
  position: absolute;
  top: 50%;
  left: 100%;
  /*  */
  transform: translate3d(-100%, -50%, 0);
  transition: all 0.5s;
}

.drawer-button-close {
  position: absolute;
  top: 50%;
  left: 100%;
  /*  */
  transform: translate3d(-100%, -50%, 0) rotateX(180deg);
  transition: all 0.5s;
}

/*video::-webkit-media-controls-play-button {
  display: none;
}
video::-webkit-media-controls-timeline {
  display: none;
}*/
</style>



